// https://developer.mozilla.org/en-US/docs/Web/CSS/@media/any-hover#syntax
const isHoverSupported = () => matchMedia("(any-hover: hover)").matches
const isSamsung = () => /Android/i.test(navigator.userAgent)

// assuming that device has a mouse if it supports hover
export const isDNDSupported = () =>
  isHoverSupported() &&
  // Samsung-specific bugfix: https://www.ctrl.blog/entry/css-media-hover-samsung.html
  !isSamsung()
