<script lang="ts">
  import type { ComponentType } from "svelte"
  import { IconButton } from "@lib/elements"
  import CameraOrPhoto from "./CameraOrPhoto.svelte"
  import PhotoDropzone from "./PhotoDropzone.svelte"
  import PhotoGift from "./PhotoGift.svelte"

  export let showSkip: boolean = false
  export let isGift: boolean = false

  let screen: ComponentType<CameraOrPhoto | PhotoDropzone | PhotoGift> = isGift
    ? (PhotoGift as ComponentType<PhotoGift>)
    : (CameraOrPhoto as ComponentType<CameraOrPhoto>)

  const toggle = () =>
    (screen = isGift
      ? (PhotoGift as ComponentType<PhotoGift>)
      : (CameraOrPhoto as ComponentType<CameraOrPhoto>))
  const dnd = () => (screen = PhotoDropzone as ComponentType<PhotoDropzone>)
</script>

<div>
  {#if screen !== CameraOrPhoto && !isGift}
    <IconButton class="back" icon="arrow-left" size="sm" on:click={toggle} />
  {/if}
  <!-- TODO: solve 'never' type issue, ts(2322)-->
  <svelte:component this={screen} on:dnd={dnd} {showSkip} {isGift} on:webcam on:photo on:skip />
</div>

<style lang="postcss">
  div {
    @apply relative flex h-full w-full flex-col items-center;
  }

  div > :global(.back) {
    @apply absolute -top-4 -left-6 md:-top-4 md:-left-14;
  }
</style>
