<script context="module" lang="ts">
  const tips = [
    {
      title: "Face uncovered",
      svg: FaceSvg,
    },
    {
      title: "Head in the frame",
      svg: FrameSvg,
    },
    {
      title: "Look straight",
      svg: LookSvg,
    },
    {
      title: "Even lighting",
      svg: LightSvg,
    },
  ]
</script>

<script lang="ts">
  import { createEventDispatcher } from "svelte"
  import { Button, Dropzone } from "@lib/elements"
  import FaceSvg from "./img/face.svelte"
  import FrameSvg from "./img/frame.svelte"
  import LightSvg from "./img/light.svelte"
  import LookSvg from "./img/look.svelte"

  const dispatch = createEventDispatcher<{
    webcam: boolean
    dnd: void
    photo: File
    skip: void
  }>()
