<div class="photo-gift">
  <Dropzone accept="image/*" on:input={({ detail }) => dispatch("photo", detail[0])}>
    <p>Tips for photo</p>
    <ul>
      {#each tips as tip}
        <li>
          <svelte:component this={tip.svg} fill="#ff0000" />
          <span>{tip.title}</span>
        </li>
      {/each}
    </ul>
    <hr />
    <Button size="lg" as="label" class="add-photo">
      Add photo
      <input
        type="file"
        accept="image/*"
        hidden
        on:input={({ currentTarget }) =>
          // @ts-expect-error: Object is possibly 'null'.ts(2531)
          dispatch("photo", currentTarget.files[0])}
      />
    </Button>
    <p class="dnd-text">or drop file here to upload</p>
  </Dropzone>
</div>

