<script lang="ts">
  import { createEventDispatcher } from "svelte"
  import { Button, Dropzone } from "@lib/elements"

  const dispatch = createEventDispatcher<{
    photo: File
  }>()
</script>

<section>
  <h2>Upload photo</h2>
  <Dropzone accept="image/*" on:input={({ detail }) => dispatch("photo", detail[0])}>
    <div>
      <Button as="label" size="lg">
        Add photo
        <input
          type="file"
          accept="image/*"
          hidden
          on:input={({ currentTarget }) =>
            // @ts-expect-error: Object is possibly 'null'.ts(2531)
            dispatch("photo", currentTarget.files[0])}
        />
      </Button>
      <span>or drag it here</span>
    </div>
  </Dropzone>
</section>

<style lang="postcss">
  section {
    @apply flex h-full w-full flex-col items-center justify-center gap-4;
  }

  h2 {
    @apply text-base md:text-xl;
  }

  div {
    @apply flex flex-col items-center gap-4 whitespace-nowrap;
  }
</style>
