<script lang="ts">
  import { createEventDispatcher } from "svelte"
  import { InlineSVG, cx } from "@lib/elements"
  import Frame from "./frame.svg"

  export let accept = "*"

  let active = false
  let valid = true

  const dispatch = createEventDispatcher<{ input: FileList }>()

  const validate = (transfer: DataTransfer | null): transfer is DataTransfer =>
    Boolean(transfer && [...transfer.items].every((i) => re.test(i.type)))

  const setFrameBorderRadius = (svg: SVGElement) => {
    const r = getComputedStyle(svg).borderRadius.replace("px", "")
    if (!r) return

    const rect = svg.querySelector("rect")!
    rect.setAttribute("rx", r)
    rect.setAttribute("ry", r)
  }

  $: re = new RegExp(accept)
  $: !active && (valid = true)
</script>

<form class={cx("dropzone-form", active && "active")} on:submit|preventDefault {...$$restProps}>
  <label
    class="tint-dropzone"
    class:active
    class:valid
    on:dragenter|preventDefault={() => (active = true)}
    on:dragover|preventDefault
    on:dragleave|preventDefault={() => (active = false)}
    on:dragenter|preventDefault={({ dataTransfer }) => (valid = validate(dataTransfer))}
    on:drop|preventDefault={() => (active = false)}
    on:drop|preventDefault={({ dataTransfer }) =>
      validate(dataTransfer) && dispatch("input", dataTransfer.files)}
  >
    <InlineSVG src={Frame} on:load={({ detail }) => setFrameBorderRadius(detail)} />

    <input
      type="file"
      {accept}
      hidden
      on:input={({ currentTarget }) =>
        // @ts-expect-error: Object is possibly 'null'.ts(2531)
        dispatch("input", currentTarget.files)}
    />
  </label>
  <div>
    <slot />
  </div>
</form>

<style lang="postcss">
  form {
    @apply relative h-[18.5rem] w-full text-[#2d2d36];

    &.active > div {
      @apply pointer-events-none opacity-0;
    }

    &:hover :global(rect) {
      @apply fill-[#f3f3f3] stroke-[#737376];
    }
  }

  label {
    @apply relative block h-full w-full cursor-pointer rounded-lg bg-[#f8f8f8];

    & :global(svg) {
      @apply h-full w-full rounded-[inherit];
    }
    & :global(rect) {
      @apply absolute top-0 left-0 h-full w-full fill-[#f8f8f8] stroke-[#dcdcdd] transition-colors;
    }

    &.active :global(rect),
    &:hover :global(rect) {
      @apply fill-[#f3f3f3] stroke-[#737376];
    }

    &::after {
      @apply pointer-events-none absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-center text-base transition-opacity;
      content: "Drop file here to upload";
    }
    &:not(.active)::after {
      @apply opacity-0 content-[""];
    }
    &:not(.valid)::after {
      content: "Unsupported file type";
    }
    &:not(.valid) :global(rect) {
      @apply fill-[#fcededf3] stroke-[#e64f4f];
    }
  }

  div {
    @apply absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 transition-opacity;
  }
</style>
