<script context="module" lang="ts">
  const tips = ["Face uncovered", "Head in the frame", "Look straight", "Even lighting"]
</script>

<script lang="ts">
  import { createEventDispatcher } from "svelte"
  import { analytics } from "@lib/analytics"
  import { Button } from "@lib/elements"
  import { requestCameraPermissions } from "@lib/utils"
  import { isMobileDevice } from "@lib/utils/is-mobile-device"
  import FaceSvg from "./img/face.svelte"
  import FrameSvg from "./img/frame.svelte"
  import LightSvg from "./img/light.svelte"
  import LookSvg from "./img/look.svelte"
  import { isDNDSupported } from "./utils"

  export let showSkip: boolean = false

  const tips = [
    {
      title: "Face uncovered",
      svg: FaceSvg,
    },
    {
      title: "Head in the frame",
      svg: FrameSvg,
    },
    {
      title: "Look straight",
      svg: LookSvg,
    },
    {
      title: "Even lighting",
      svg: LightSvg,
    },
  ]

  const dnd = isDNDSupported()
  const isMobile = isMobileDevice()

  const dispatch = createEventDispatcher<{
    webcam: boolean
    dnd: void
    photo: File
    skip: void
  }>()
</script>

<section class="tint-camera-or-photo">
  <article>
    <h1>Tips for virtual try-on</h1>
    <div class="tips-container">
      {#each tips as tip}
        <div class="tip">
          <svelte:component this={tip.svg} />
          <span>{tip.title}</span>
        </div>
      {/each}
    </div>
  </article>
  <form on:submit|preventDefault>
    <div class="buttons" style={isMobile ? "flex-direction: column-reverse !important;" : ""}>
      <Button
        variant={isMobile ? "secondary" : "primary"}
        size="lg"
        on:click={() => requestCameraPermissions().then((granted) => dispatch("webcam", granted))}
      >
        Live camera
      </Button>
      {#if dnd}
        <Button
          variant={isMobile ? "primary" : "secondary"}
          size="lg"
          on:click={() => dispatch("dnd")}
          on:click={() => analytics.upload_photo_clicked()}
        >
          Upload photo
        </Button>
      {:else}
        <Button as="label" variant={isMobile ? "primary" : "secondary"} size="lg">
          Upload photo
          <input
            type="file"
            accept="image/*"
            hidden
            on:click={() => analytics.upload_photo_clicked()}
            on:input={({ currentTarget }) =>
              // @ts-expect-error: Object is possibly 'null'.ts(2531)
              dispatch("photo", currentTarget.files[0])}
          />
        </Button>
      {/if}
    </div>
    {#if showSkip}
      <!-- svelte-ignore a11y-click-events-have-key-events -->
      <p on:click={() => dispatch("skip")}>Continue with uploaded photo</p>
    {/if}
  </form>
</section>

<style lang="postcss">
  section {
    @apply flex flex-col items-center;
  }

  article {
    @apply flex flex-col items-center justify-center;
  }

  h1 {
    @apply mb-5 text-base md:text-xl;
  }

  .tips-container {
    @apply mb-6 grid grid-cols-2 gap-4;
  }

  .tip {
    @apply mb-1 flex w-[7.5rem] flex-col items-center text-sm;
  }

  .tip :global(svg) {
    @apply text-[#16161a];
  }

  .tip span {
    @apply mt-1;
  }

  form,
  form .buttons {
    @apply mt-0 flex w-full max-w-[17.375rem] flex-col !important;
  }

  form .buttons :global(button),
  form .buttons :global(label) {
    @apply w-full;
  }

  .tint-camera-or-photo :global(.primary) {
    @apply mb-3;
  }

  .tint-camera-or-photo :global(.secondary) {
    @apply mb-6;
  }

  form p {
    @apply cursor-pointer text-center text-sm font-bold text-gray-800 opacity-60;
  }
</style>
